<template>
  <div id="app">
    <div class="container-fluid" style="padding: unset">
      <Header img="" school='Go语言实战抽奖系统'></Header>
    </div>
    <div class='tap'>
      <HorizontalTap class='center' :data='data2' :active="4" style="padding-left: 100px;"></HorizontalTap>
    </div>
    <div class="row mt-5 " >
      <div v-if="pdfFineFlg">
        <!--一个是别人的，一个是我的-->
        <i class="el-icon-error close-btn"  @click="closePdf" ></i>
        <PDF v-if="pdfFineFlg" :pdf-url="pdfFineUrl" :is-contrast="pdfFineIsContrast" :display-id="displayFineId"></PDF>
        <PDF v-if="pdfMyFlg" :pdf-url="pdfMyUrl" :is-contrast="pdfMyIsContrast" :display-id="displayMyId" style="margin-left: 50%"></PDF>
      </div>
      <div class="col-md-8 col-md-offset-2 text-center shadow-radius">
        <div class="col-md-12 col-xs-12 text-center">
          <div class="col-md-5 col-xs-12 text-center">
            <img class="growth-img" src="../../../../assets/img/growth.png">
          </div>
          <div class="col-md-7 col-xs-12" v-if="classPM && schoolPM && projectPM">
            <div class="col-md-12 text-center pt-1">
              <h1 class="font-bold project-name">{{projectName}}</h1>
            </div>
            <div class="col-md-4 text-center pingfen-mt">
              <el-progress type="circle" :percentage="projectPM" color="rgb(19, 206, 102)" style="margin-left: 5%;margin-right: 5%"></el-progress>
              <span class="col-md-12 mt-3 font-small">你在本项目中排名{{rankInfo.projectRank}}/{{rankInfo.projectHeadCount}}</span>
            </div>
            <div class="col-md-4 text-center pingfen-mt">
              <el-progress type="circle" :percentage="schoolPM" color="#e6a23c" style="mrgb(32, 160, 255);argin-left: 5%;margin-right: 5%"></el-progress>
              <span class="col-md-12 mt-3 font-small">你在学校中排名{{rankInfo.schoolRank}}/{{rankInfo.schoolHeadCount}}</span>
            </div>
            <div class="col-md-4 text-center pingfen-mt">
              <el-progress type="circle" :percentage="classPM" color="rgb(32, 160, 255)" style="margin-left: 5%;margin-right: 5%"></el-progress>
              <span class="col-md-12 mt-3 font-small">你在班级中排名{{rankInfo.classRank}}/{{rankInfo.classHeadCount}}</span>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="row">
      <div class="mt-7 pl-8 col-xs-12 col-md-offset-2 col-md-8">
        <el-timeline>
          <el-timeline-item
            v-for="(activity, index) in activities"
            :key="index"
            :icon="activity.icon"
            :type="activity.type"
            :color="activity.color"
            :size="activity.size"
            :placement="'top'"
            :timestamp="activity.timestamp">
            <div class="font-bold font-large" style="margin-top: -12px" v-if="activity.dotType === 'stage'">
              <div><a title="该阶段反馈报告">{{activity.content}}<i class="iconwendangmoban iconfont  ml-3" style="color: #6612cf;"></i></a> </div>
            </div>
            <div v-else style="margin-top: -25px" >
              <div>
                {{activity.content}}
                <span v-if="activity.status === 0" class="task-status" style="background-color: #409eff">进行中</span>
                <span v-if="activity.status === -1" class="task-status" style="background-color: rgb(255, 73, 73)">未开始</span>
                <span v-if="activity.status === 1" class="task-status" style="background-color: rgb(19, 206, 102)">已完成</span>
                <span v-if="activity.status === 2" class="task-status" style="background-color: rgb(255, 73, 73)">未完成</span>
              </div>
              <div v-if="!activity.fineMark" class="row pl-6 pt-4">
                <div v-if="!activity.noDataFlg">
                <!--若果有最优成果物-->
                  <div v-if="activity.fineUserName" class="col-md-3 col-md-offset-1 text-center">
                  <a title="评分最高"><i class="iconzuikuaide iconfont" style="font-size: 20px;color:gold "></i></a>
                  <div class="rank-img mt-4" :style="{backgroundImage:'url('+ activity.fineUserPhotoUrl + ')'}">
                    <div class="rank-user">{{activity.fineUserName}}</div>
                  </div>
                  <div class="text-center mt-4 font-small">
                    <a>{{activity.fineOutputNameFormat}}</a>
                  </div>
                  <div class="text-center mt-2" v-if="activity.canLookFlg">
                    <i class="iconfont iconliulan action-icon" style="font-size: 14px" @click="goPdf(activity)"></i>
                    <!--<i class="el-icon-download ml-3 action-icon" style="font-size: 19px"></i>-->
                  </div>
                </div>
                <!--若无最优成果物-->
                  <div v-else class="col-md-3 col-md-offset-1 text-center">
                  <a title="评分最高"><i class="iconzuikuaide iconfont" style="font-size: 20px;color:gold "></i></a>
                  <div class="rank-img mt-4" style="background-image: url('http://39.100.37.131/zhehang/image/userhead/3741223c7f2b48798701560f09fe9757.png')">
                    <div class="rank-user">暂无排名</div>
                  </div>
                </div>
                  <div class="col-md-2">
                  </div>
                  <div class="col-md-3 text-center">
                  <a title="我的成果物"><i class="el-icon-user" style="font-size: 25px;color: #6612cf"></i></a>
                  <div class="rank-img mt-4" :style="{backgroundImage:'url('+ activity.userPhotoUrl +')'}">
                    <div class="rank-user">我</div>
                  </div>
                  <div v-if="activity.canDownFlg" class="text-center mt-4 font-small">
                    <a>{{activity.outputNameFormat}}</a>
                  </div>
                  <div v-else class="text-center mt-4">
                    <a>未上传成果物</a>
                  </div>
                  <div class="text-center mt-2" v-if="activity.canDownFlg">
                    <!--<i class="iconfont iconliulan action-icon" style="font-size: 14px"></i>-->
                    <i class="el-icon-download  action-icon" style="font-size: 19px" @click="downLoadOutPut(activity)"></i>
                  </div>
                </div>
                </div>
                <div v-else class="col-md-6 text-center">
                  <img style="border-radius: unset;width: 100%;padding:20px" src="../../../../assets/img/no_data1.png">
                  <div class="pointer font-small">暂无任何信息</div>
                </div>
              </div>
              <div v-else class="row pl-6 pt-4">
                <div class="col-md-3 col-md-offset-1 text-center">
                  <a title="评分最高"><i class="iconzuikuaide iconfont" style="font-size: 20px;color:gold "></i></a>
                  <div class="mark-per mt-4" :style="{ color: customColorMethod(activity.fineMark) }">
                    {{activity.fineMark}}
                  </div>
                  <div class="text-center mt-4 font-small">
                    <a>{{activity.fineMarkUserName}}</a>
                  </div>
                </div>
                <div class="col-md-2">
                </div>
                <div class="col-md-3 text-center">
                  <a title="我的得分"><i class="el-icon-user" style="font-size: 25px;color: #6612cf"></i></a>
                  <div class="mark-per mt-4" :style="{ color: customColorMethod(activity.mark) }">
                    {{activity.mark}}
                  </div>
                  <div class="text-center mt-4 font-small">
                    <a>我</a>
                  </div>
                </div>
              </div>
            </div>
          </el-timeline-item>
        </el-timeline>
        <!--</div>-->
      </div>
    </div>
  </div>
</template>

<script>
import Header from '../../../../components/school-training/Header.vue'
import HorizontalTap from '../../../../components/tap/HorizontalTap.vue'
import PDF from '../../../../components/learning-library/pdf'
import { Loading } from 'element-ui'
export default {
  name: 'app',
  components: {
    Header,
    HorizontalTap,
    PDF
  },
  mounted () {
    this.loadData()
  },
  data () {
    return {
      taskList: null,
      data2: [{
        name: '执行任务',
        url: 'task-list.html',
        type: '_self'
      }, {
        name: '任务单',
        url: 'work-order.html',
        type: '_self'
      }, {
        name: '日报任务',
        url: 'daily-tasks.html',
        type: '_self'
      }, {
        name: '相关资料',
        url: 'learning-library.html',
        type: '_self'
      }, {
        name: '成长轨迹',
        url: '',
        type: '_self'
      }],
      activities: [],
      projectName: '',
      pdfFineFlg: false,
      pdfMyFlg: false,
      pdfFineUrl: '',
      pdfMyUrl: '',
      pdfFineIsContrast: false,
      pdfMyIsContrast: false,
      displayFineId: 'the-canvas',
      displayMyId: 'the-canvas-contrast',
      rankInfo: null,
      classPM: null,
      projectPM: null,
      schoolPM: null
    }
  },
  methods: {
    loadData () {
      let me = this
      me.projectName = localStorage.getItem('projectName')
      this.$http.post('/doproject/growthTrajectoryInfo', {
        'teamId': localStorage.getItem('teamId')
      }, res => {
        console.log(res)
        if (res.success) {
          me.taskList = res.data
          me.formatData()
        }
      })
      me.$http.post('/doproject/rankStatisticsInfo', { 'teamId': localStorage.getItem('teamId') }, res => {
        if (res.success) {
          me.rankInfo = res.data
          me.classPM = Math.floor(100 - me.rankInfo.classRank / me.rankInfo.classHeadCount * 100)
          me.projectPM = Math.floor(100 - me.rankInfo.projectRank / me.rankInfo.projectHeadCount * 100)
          me.schoolPM = Math.floor(100 - me.rankInfo.schoolRank / me.rankInfo.schoolHeadCount * 100)
        }
      })
    },
    formatData () {
      let obj = {}
      this.activities = []
      for (var i = 0; i < this.taskList.length; i++) {
        obj = { content: this.taskList[i].stageName, timestamp: '', color: '#FF6600', size: 'large', icon: '', dotType: 'stage' }
        this.activities.push(obj)
        for (var j = 0; j < this.taskList[i].detail.length; j++) {
          obj = this.taskList[i].detail[j]
          if (obj.status === -1 || obj.status === 2) {
            this.formatDot(obj, 'el-icon-close', 'rgb(255, 73, 73)', '')
          } else if (obj.status === 1) {
            this.formatDot(obj, 'el-icon-check', 'rgb(19, 206, 102)', obj.updateTime)
          } else if (obj.status === 0) {
            this.formatDot(obj, 'el-icon-more', '#409eff', '')
          }
          this.activities.push(obj)
        }
      }
    },
    formatDot (obj, icon, color, timestamp) {
      obj.content = obj.stepName
      obj.timestamp = timestamp
      obj.size = 'normal'
      obj.dotType = 'step'
      obj.outputNameFormat = this.formatOutputName(obj.outputName)
      if (obj.outputNameFormat.length > 15) {
        obj.outputNameFormat = obj.outputNameFormat.substr(0, 15) + '...'
      }
      obj.fineOutputNameFormat = this.formatOutputName(obj.fineOutputName)
      if (obj.fineOutputNameFormat.length > 15) {
        obj.fineOutputNameFormat = obj.fineOutputNameFormat.substr(0, 15) + '...'
      }
      obj.icon = icon
      obj.color = color
      if (obj.fineOutputName == null && obj.outputName == null) {
        // 都没有成果物时显示暂无
        obj.isContrast = false
        obj.canLookFlg = false
        obj.noDataFlg = true
        obj.canDownFlg = false
      } else if (obj.fineOutputName !== null && obj.outputName == null) {
        // 有最优的成果物，没有本人的成果物
        obj.isContrast = false
        obj.canLookFlg = true
        obj.noDataFlg = false
        obj.canDownFlg = false
      } else if (obj.fineOutputName == null && obj.outputName !== null) {
        // 无最优的成果物，有本人的成果物
        obj.isContrast = false
        obj.canLookFlg = false
        obj.noDataFlg = false
        obj.canDownFlg = true
      } else if (obj.fineOutputName !== null && obj.outputName !== null) {
        // 有最优的成果物，有本人的成果物
        obj.isContrast = true
        obj.canLookFlg = true
        obj.noDataFlg = false
        obj.canDownFlg = true
      }
    },
    formatOutputName (name) {
      if (name !== null) {
        let outputName = name.split('$_$')
        name = outputName[1]
        return name
      } else {
        return ''
      }
    },
    customColorMethod (percentage) {
      if (percentage < 60) {
        return 'rgb(19, 206, 102)'
      } else if (percentage <= 89 && percentage >= 60) {
        return '#6612cf'
      } else {
        return 'rgb(255, 73, 73)'
      }
    },
    goPdf (obj) {
      let me = this
      this.pdfFineFlg = false
      this.pdfMyFlg = false
      // me.$http.post('/assistant/openpdf', { 'outputName': obj.fineOutputName }, res => {
      //   console.log(res)
      // })
      // return false
      if (obj.isContrast) {
        me.$nextTick(() => {
          this.pdfFineUrl = 'http://39.100.37.131/file/pdffile/' + obj.fineOutputName + '.pdf'
          this.pdfMyUrl = 'http://39.100.37.131/file/pdffile/' + obj.outputName + '.pdf'
          this.pdfFineIsContrast = true
          this.pdfMyIsContrast = true
          this.pdfFineFlg = true
          this.pdfMyFlg = true
        })
      } else {
        this.pdfFineUrl = 'http://39.100.37.131/file/pdffile/' + obj.fineOutputName + '.pdf'
        this.pdfFineIsContrast = false
        this.pdfFineFlg = true
      }
    },
    closePdf () {
      this.pdfFineFlg = false
      this.pdfMyFlg = false
    },
    downLoadOutPut (obj) {
      const loading = Loading.service({
        lock: true,
        text: '下载中请稍候...',
        spinner: 'el-icon-loading',
        // background: 'rgba(0, 0, 0, 0.7)'
        background: 'rgba(f, 0, 0, 0.7)'
      })
      this.$axios({
        method: 'post',
        url: 'http://39.100.37.131:8085/lying-web/doproject/output/downloadFile',
        data: { 'filename': obj.outputName },
        responseType: 'blob',
        headers: {
          token: localStorage.getItem('token'),
          'Content-Type': 'application/json; charset=UTF-8'
        }
      }).then(
        (response) => {
          var blob = new Blob([response.data])
          var downloadElement = document.createElement('a')
          var href = window.URL.createObjectURL(blob)
          downloadElement.href = href
          downloadElement.download = obj.outputName.split('$_$')[1]
          document.body.appendChild(downloadElement)
          downloadElement.click()
          document.body.removeChild(downloadElement)
          window.URL.revokeObjectURL(href)
          setTimeout(() => {
            loading.close()
          }, 1000)
        }
      ).catch(
        (error) => {
          console.log(error)
          setTimeout(() => {
            loading.close()
          }, 1000)
        }
      )
    }
  }
}
</script>
<style scoped lang="scss">
  @media (min-width: 768px) and (max-width: 1400px) {
    .pingfen-mt{
      margin-top: 64px;
    }
  }
  @media (min-width: 1401px) and (max-width: 1601px) {
    .pingfen-mt{
      margin-top: 120px;
    }
    .project-name{
      padding-top: 10px;
    }
  }
  @media (min-width: 1602px) and (max-width: 3000px) {
    .pingfen-mt{
      margin-top: 20%;
    }
    .project-name{
      padding-top: 10%;
    }
  }
  .task-status{
    margin-left: 5px;
    font-size: 8px;
    font-weight: bold;
    height: 20px;
    line-height: 20px;
    color: white;
    border-radius: 15px;
    padding: 2px;
  }
  .rank-img{
    border-radius: 50%;
    width:100px;
    height:100px;
    margin: 0px auto;
    background-size: contain;
    .rank-user{
      color:rgba(255,255,255,1);
      width:100%;
      height:100%;
      background-color: #000000;
      border-radius:50%;
      opacity:0.68;
      display:none;
      z-index:101;
      font-family:PingFang-SC-Regular;
      font-weight:400;
      padding-top: 42px;
      cursor:pointer;
    }
  }
  .rank-img:hover{
    .rank-user{
      display: block;
    }
  }
  .action-icon{
    cursor: pointer;
  }
  .growth-img{
    width:100%;
    height:100%;
    cursor:pointer;
  }
  .mark-per{
    font-size: 50px;
    font-weight: bold;
  }
  .project-name{
    background-image:-webkit-linear-gradient(left, white, #c543d8, #6612cf,white);
    -webkit-background-clip:text;
    -webkit-text-fill-color:transparent;
  }
  .close-btn{
    z-index: 1001;
    position: fixed;
    top: 3%;
    left: 95%;
    color: red;
    font-size: 30px;
  }
</style>
<style>
  .el-timeline-item__timestamp {
    color: #909399;
    line-height: 1;
    font-size: 13px;
    position: absolute;
    left: -18%;
    /*top: 5%;*/
  }
  .el-timeline-item {
    position: relative;
    padding-bottom: 80px!important;
  }
  .el-timeline-item__node--large {
    left: -4px!important;
    width: 18px!important;
    height: 18px!important;
  }
  .el-timeline-item__tail {
    position: absolute;
    left: 4px;
    height: 100%;
    border-left: 3px solid #E4E7ED!important;
  }
</style>
